import { BASE_URL } from "../constants"
import { BusinessUnitConfigQualityFactor, QualityFactorData, QualityFactorEntity, QualityFactorInsertEntity, QualityFactorUpdateEntity, DashboardQualityFactorLevel1, DashboardQualityFactorLevel2, DashboardQualityFactorLevel3, FormBusinessUnitQualityFactor, FormLinkupQualityFactor, LinkUpConfigQualityFactor, } from "../entities/QualityFactorEntity"
import FetchUtils from "../utils/fetch"

class QualityFactorService {
    static async GetAllQualityFactor(link_up_id?: string): Promise<QualityFactorEntity[]> {

        let uri = `${BASE_URL}/api/extension/quality_factor/v1`
        if (link_up_id != null) {
            uri += `?link_up_id=${link_up_id}`
        }

        const resp = await FetchUtils.fetchAuth(uri)
        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        const data: QualityFactorEntity[] = Array.isArray(resp.data) ? resp.data : []
        for (let i = 0; i < data.length; i++) {
            if (typeof data[i].quality_factor_id != "number") { throw new Error("invalid quality_factor_id value") }
            if (typeof data[i].link_up_id != "number") { throw new Error("invalid link_up_id value") }
            if (typeof data[i].link_up != "string") { throw new Error("invalid link_up value") }
            if (typeof data[i].parameter_name != "string") { throw new Error("invalid parameter_name value") }
            if (typeof data[i].quality_factor_tipe != "string") { throw new Error("invalid quality_factor_tipe value") }
            if (data[i].quality_factor_warn != undefined && typeof data[i].quality_factor_warn != "number") { throw new Error("invalid quality_factor_warn value") }
            if (data[i].quality_factor_alarm != undefined && typeof data[i].quality_factor_alarm != "number") { throw new Error("invalid quality_factor_alarm value") }
            if (data[i].quality_factor_bottom_warn != undefined && typeof data[i].quality_factor_bottom_warn != "number") { throw new Error("invalid quality_factor_bottom_warn value") }
            if (data[i].quality_factor_bottom_alarm != undefined && typeof data[i].quality_factor_bottom_alarm != "number") { throw new Error("invalid quality_factor_bottom_alarm value") }
            if (typeof data[i].quality_factor_display_type != "string") { throw new Error("invalid quality_factor_display_type value") }
            if (typeof data[i].quality_factor_unit != "string") { throw new Error("invalid quality_factor_unit value") }
        }

        return data
    }

    static async AddQualityFactor(data: QualityFactorInsertEntity): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/quality_factor/v1`,
            {
                method: 'POST',
                body: JSON.stringify({ ...data }),
                headers: { "Content-Type": "application/json" }
            }
        )

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }

    static async UpdateQualityFactor(quality_factor_id: number, fields: QualityFactorUpdateEntity): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/quality_factor/v1`,
            {
                method: 'PATCH',
                body: JSON.stringify({ quality_factor_id, fields }),
                headers: { "Content-Type": "application/json" }
            }
        )

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }

    static async DeleteQualityFactor(quality_factor_id: number): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/quality_factor/v1`,
            {
                method: 'DELETE',
                body: JSON.stringify({ quality_factor_id }),
                headers: { "Content-Type": "application/json" }
            }
        )

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }

    static async GetLinkUpConfig(link_up_id?: number): Promise<LinkUpConfigQualityFactor[]> {
        let uri = `${BASE_URL}/api/extension/quality_factor/v1/linkup`
        if (link_up_id != null) {
            uri += `?link_up_id=${link_up_id}`
        }

        const resp = await FetchUtils.fetchAuth(uri)
        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        if (!Array.isArray(resp.data)) {
            throw new Error("invalid link up config response")
        }
        for (let i = 0; i < resp.data.length; i++) {
            const row = resp.data[i]
            if (typeof row.link_up_id != "number") { throw new Error("invalid link_up_id value") }
            if (typeof (row.quality_factor_warn ?? 0) != "number") { throw new Error("invalid quality_factor_warn value") }
            if (typeof (row.quality_factor_alarm ?? 0) != "number") { throw new Error("invalid quality_factor_alarm value") }
        }

        return resp.data
    }

    static async UpsertLinkupConfig(link_up_id: number, fields: FormLinkupQualityFactor): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/quality_factor/v1/linkup`, {
            method: 'PATCH',
            body: JSON.stringify({ link_up_id, fields }),
            headers: { "Content-Type": "application/json" }
        })

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }

    static async GetBusinessUnitConfig(business_unit_id?: number): Promise<BusinessUnitConfigQualityFactor[]> {
        let uri = `${BASE_URL}/api/extension/quality_factor/v1/business`
        if (business_unit_id != null) {
            uri += `?business_unit_id=${business_unit_id}`
        }

        const resp = await FetchUtils.fetchAuth(uri)
        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        if (!Array.isArray(resp.data)) {
            throw new Error("invalid link up config response")
        }
        for (let i = 0; i < resp.data.length; i++) {
            const row = resp.data[i]
            if (typeof row.business_unit_id != "number") { throw new Error("invalid business_unit_id value") }
            if (typeof (row.quality_factor_warn ?? 0) != "number") { throw new Error("invalid quality_factor_warn value") }
            if (typeof (row.quality_factor_alarm ?? 0) != "number") { throw new Error("invalid quality_factor_alarm value") }
        }

        return resp.data
    }

    static async UpsertBusinessUnitConfig(business_unit_id: number, fields: FormBusinessUnitQualityFactor): Promise<{ message: string }> {
        const resp = await FetchUtils.fetchAuth(`${BASE_URL}/api/extension/quality_factor/v1/business`, {
            method: 'PATCH',
            body: JSON.stringify({ business_unit_id, fields }),
            headers: { "Content-Type": "application/json" }
        })

        if (resp.status !== 200) {
            throw new Error(resp.message)
        }

        return resp
    }
}

