            if (typeof data[i].quality_factor_red != "number") { throw new Error("invalid quality_factor_red value") }

            if (!data[i].last_quality_factor_data) { throw new Error("missing last_quality_factor_data") }
            if (typeof data[i].last_quality_factor_data.quality_factor_id !== "number") { throw new Error("invalid last_quality_factor_data.quality_factor_id") }
            if (typeof data[i].last_quality_factor_data.value !== "number") { throw new Error("invalid last_quality_factor_data.value") }
            if (data[i].last_quality_factor_data.warning_limit != undefined && typeof data[i].last_quality_factor_data.warning_limit !== "number") { throw new Error("invalid last_quality_factor_data.warning_limit") }
            if (data[i].last_quality_factor_data.alarm_limit != undefined && typeof data[i].last_quality_factor_data.alarm_limit !== "number") { throw new Error("invalid last_quality_factor_data.alarm_limit") }
